<template>
  <article class="project__item">
    <img v-bind:src="project.image" class="project__image" alt="Project's image" />
    <div class="project__desc">
      <h3>{{ project.name }}</h3>
      <a @click="viewProject" class="project__link">View more</a>
    </div>
  </article>
</template>

<script>
export default {
  props: ["project"],
  methods: {
    viewProject() {
      const order = {
        projectId: this.project.id,
        projectName: this.project.name
      };
      this.$store.dispatch('viewProject', order);
      this.$router.push(this.project.url);
    }
  }
}
</script>

