<template>
  <div class="container">
    <h2 class="projects__title">My projects<br> from last to first</h2>
<transition name="slide" mode="out-in">
      <div class="projects">

        <app-project v-for="(project, index) in projects" :project="project" :key="index"></app-project>

      </div>
</transition>

  </div>
</template>

<script>
  import Project from "./Project.vue";
  export default {
    components: {
      appProject: Project
    },
    computed: {
      projects() {
        return this.$store.getters.projects;
      }
    }
  }
</script>
