import projects from "../../data";

const state = {
  projects: []
};

const mutations = {
  "SET_PROJECTS"(state, projects) {
    state.projects = projects;
  }
};

const actions = {
  viewProject: ({commit}, order) => {
    commit("VIEW_PROJECT", order);
  },
  initProjects: ({commit}) => {
    commit("SET_PROJECTS", projects);
  }
};

const getters = {
  projects: state => {
    return state.projects;
  }
};

export default {
  state,
  mutations,
  actions,
  getters
};
