const state = {
  projects: [],
};

const mutations = {
  "VIEW_PROJECT"(state, {projectId, projectName}) {
    state.projects = [];
      state.projects.push({
        id: projectId,
        name: projectName,
      });
  },
  "SET_PROJECT"(state, order) {
    state.projects = order;
  }
};

const actions = {
  initProject: ({ commit }) => {
    commit("SET_PROJECT", projects);
  }
};

const getters = {
  projectSingle(state, getters) {
    return state.projects.map(project => {
      const record = getters.projects.find(element => element.id == project.id);
      return {
        id: project.id,
        name: project.name,
        image: record.image,
				image2: record.image2,
        stacks: record.stacks,
        lib: record.lib,
        pages: record.pages,
        desc1: record.desc1,
        desc2: record.desc2,
        desc3: record.desc3,
        desc4: record.desc4,
        desc5: record.desc5,
        desc6: record.desc6,
        desc7: record.desc7,
				desc8: record.desc8,
				desc9: record.desc9,
        desc10: record.desc10,
        desc11: record.desc11,
        desc12: record.desc12,
        link: record.link,
        github: record.github
      }
  });
}
}

export default {
  state,
  mutations,
  actions,
  getters
}
